import adsobject from "@/components/script/adsobject";

export default {
  name: "banner",
  mixins: [adsobject],
  props: {
    ads_conf: undefined
  },
  created() {
    console.log("adsbanner:", this.ads_conf)
  },
  methods: {
    on_click_ads: function () {
      this.$ga.event({
        eventCategory: 'ads_banner',
        eventAction: 'click',
        eventLabel: this.ads_conf['zone'],
        eventValue: this.ads_conf['zone']
      });
    }
  }
}
