export default {
  name: "active_code",
  data: function () {
    return {
      vip_code: undefined,
      is_subming: false,
      valid: true,
      active_code: '',
      active_code_rule: [
        v => !!v || '请输入',
      ]
    }
  },
  created() {

  }, methods: {
    on_submit: function () {
      this.is_subming = true;
      this.$axios.get("/app/active/code", {
        params: {
          code: this.active_code
        }
      }).then((response) => {
        console.log("response:", response);
        var vip_code = response.data.vip_code;
        this.is_subming = false;

      }).catch((e) => {
        this.is_subming = false;
      });
    }
  }
}
