export default {
  name: "links",
  data() {
    return {
      links_list: undefined
    }
  },
  created() {
    this.init();
    this.listener_init = this.$hub.on('event:init', () => {
      this.init();
      this.$forceUpdate();
    });
  },
  methods: {
    load_from_local: function () {
      var links = localStorage.getItem('links');
      if (links) {
        this.links_list = JSON.parse(links);
      }
      console.log('load_from_local:', this.links_list);
    },
    init: function () {
      try {
        this.links_list = this.$store.state.init.data.link_conf;
        console.log("link_conf:", this.$store.state.init.data.link_conf);
        this.update_rank();
        localStorage.setItem('links', JSON.stringify(this.links_list));
      } catch (e) {
        this.load_from_local();
      }
      if (!this.links_list) {
        this.load_from_local();
      }
    },
    update_rank: function () {
      var source_from = this.get_from_source();
      var new_link_list = [];

      var set_link_item = undefined;
      var rank = undefined;

      if (source_from) {
        for (var i = 0; i < this.links_list.length; ++i) {
          if (this.links_list[i]['id'] == source_from && this.links_list[i].rank) {
            rank = parseInt(this.links_list[i].rank);
            set_link_item = this.links_list[i];
          } else {
            new_link_list.push(this.links_list[i]);
          }
        }
      }

      console.log('update_rank:', new_link_list, set_link_item);
      if (set_link_item && !isNaN(rank)) {
        new_link_list.splice(rank - 1, 0, set_link_item)
      }
      this.links_list = new_link_list;

    }
  },
  destroyed() {
    this.listener_init.off();
  },
}
